//POST传参序列化
axios.interceptors.request.use((config) => {
    // if(config.method  === 'post'){
    //     config.data = qs.stringify(config.data)
    // }
    return config
},(error) =>{
    return Promise.reject(error)
})

//返回状态判断
axios.interceptors.response.use((res) =>{
    if(res.status != 200){
        return Promise.reject(res)
    }
    return res.data.body
}, (error) => {
    if (error.response) {
      switch (error.response.status) {
        case 403:
            router.push({ path: '/login' })
      }
    }
    return Promise.reject(error)
})

export function fetch(Interface, params) {
    var resParams = {
        'head':{
            'version':"0.01",
            'msgtype':"request",
            'interface': Interface,
            'remark':""
        },
        'params':{
            "system":"SALESAPP",
            
        }
    }
    for(var i in params){
        resParams.params[i] = params[i]
    }
    return new Promise((resolve, reject) => {
// console.log(util.api);
        axios.post(util.api+'/channel_org_interface', resParams)
            .then(response => {
                resolve(response)
            }, err => {
                reject(err)
            })
            .catch((error) => {
               reject(error)
            })
    })
}
function get_cookie(Name) {
   var search = Name + "="//查询检索的值
   var returnvalue = "";//返回值
   if (document.cookie.length > 0) {
        var sd = document.cookie.indexOf(search)
        if (sd!= -1) {
            sd += search.length
            let end = document.cookie.indexOf(";", sd)
            if (end == -1)
                end = document.cookie.length
             //unescape() 函数可对通过 escape() 编码的字符串进行解码。
            returnvalue=unescape(document.cookie.substring(sd, end))
        }
   } 
   return returnvalue
}


export function fetchsale(Interface, params ) {
    
    var time = parseInt(new Date().getTime()/1000)
   if(Interface == 'sale_merlogin'){
       var resParams = {
       'head':{
           'version':"0.01",
           'msgtype':"request",
           'interface': Interface,
           'remark':""
       },
       'params':{
            "system":"SALESAPP",
                "time": time,
       }
       }
   }else{
       var resParams = {
       'head':{
           'version':"0.01",
           'msgtype':"request",
           'interface': Interface,
           'remark':""
       },
       'params':{
            "system":"SALESAPP",
               "login_token": sessionStorage.getItem('token'),
               "time":time,
       }
       }
   }
   
   
   
    for(var i in params){
       if ( params[i] != '' || params[i].length < 0 || params[i].length !== 0 ){
           resParams.params[i] = params[i]
       }
    }

   var sign = util.getSignMd5(resParams);
   resParams.params['sign'] = sign
    
    return new Promise((resolve, reject) => {
        axios.post(util.apicc + '/index/index', resParams )
            .then(response => {
                resolve(response)
            }, err => {
                reject(err)
            })
            .catch((error) => {
               reject(error)
            })
    })
}

let Fusername , FuserId

export function crossDomain(Interface, params ) {
    // if(!Fusername) Fusername = get_cookie('username')
    // Fusername = 'asd'
    if(!FuserId) FuserId = get_cookie('useruuid')
    var resParams = {
        'head':{
            'version':"0.01",
            'msgtype':"request",
            'interface': Interface,
            'remark':""
        },
        'params':{
        "system":"HJXMBA",
            "login_user_id":FuserId,
            "login_token":"12345678911",
            "login_system_id":"HJXMBA",
            "login_username":''
        }
    }
    for(var i in params){
        resParams.params[i] = params[i]
    }
    return new Promise((resolve, reject) => {
        axios.post(util.api + '/index/index', resParams )
            .then(response => {
                resolve(response)
            }, err => {
                reject(err)
            })
            .catch((error) => {
               reject(error)
            })
    })
}

